<template>
  <v-layout>
    <template v-if="occur">
      <template v-if="winner !== true">
        <v-flex xs6 sm6 md6 lg6 x6>
          <v-card
            class="mx-auto"
            max-width="500"
            tile
          >
          <v-card-title>Nivel {{ this.$route.params.game }}</v-card-title>
          <v-card-text>
            <div>Acciones</div>
            <v-card-actions>
              <v-btn 
                text
                color="purple"
                @click="add_step('izquierda')"
              >
                <v-icon>arrow_back</v-icon>
              </v-btn>

              <v-btn
                text
                color="purple"
                @click="add_step('abajo')"
              >
                <v-icon>arrow_downward</v-icon>
              </v-btn>

              <div class="flex-grow-1"></div>

              <v-btn 
                text
                color="purple"
                @click="add_step('derecha')"
              >
                <v-icon>arrow_forward</v-icon>
              </v-btn>

              <v-btn
                text
                color="purple"
                @click="add_step('arriba')"
              >
                <v-icon>arrow_upward</v-icon>
              </v-btn>
            </v-card-actions>
            <v-card-actions>
              <v-btn 
                text
                class="mx-auto"
                color="purple"
                @click="play()"
              >
                <v-icon>play_circle_filled</v-icon>
              </v-btn>

            </v-card-actions>
          </v-card-text>
    
            <v-list fill-height>
              <v-list-item-group v-model="steps" color="primary">
                <v-list-item
                  v-for="(step, i) in steps"
                  :key="i"
                >
                  <v-list-item-icon>
                    <v-icon v-text="step.icon"></v-icon>
                  </v-list-item-icon>
                  <v-list-item-content>
                    
                  </v-list-item-content>
                  <v-list-item-title v-text="step.text"></v-list-item-title>

                  <v-badge left>
                      <template v-slot:badge>
                        <span> {{ step.count[step.count.length-1].counter }}</span>
                      </template>
                  </v-badge>
                </v-list-item>
              </v-list-item-group>
            </v-list>
          </v-card>
        </v-flex>
        <v-flex xs8 sm8 md8 lg8 xl8>
          <template v-if="!winner">
            <div class="game"></div>
          </template>
        </v-flex>
      </template>
