<script>
import axios from 'axios';
import writeJsonFile from 'write-json-file';
export default {
    name: 'Game',
    data: () => ({
      occur: true,
      playing: false,
      winner: false,
      steps: [],
      level: {
        player : {},
        blocks : [],
        goals  : [],
        walls  : []
      },
      direction: {x:0,y:0},
      start: null,
      imgPlayer: require("@/assets/img/player-retro.png"),
      imgGoal: require("@/assets/img/goals.png"),
      imgObstacule: require("@/assets/img/obstacule.jpg")
    }),
    mounted(){
      let $this = this;
      axios.get('juego/list', { params: { valor: this.$route.params.game } })
      .then(res => {
        if (res.data.length != 0) {
          $this.level.player = res.data[0].player;
          $this.level.goals = res.data[0].goals;
          $this.level.blocks = res.data[0].blocks;
          if(res.data[0].walls.length !== 0){
            res.data[0].walls.map(wall => {
              $this.level.walls.push(wall)
            });
          }

          this.update()
        }else {
          $this.occur = false;
        }
        
      })
      .catch(e => console.log(e));

      this.Sokoban(8,8);
    },
    methods: {
      update(){
          for(var i=0,l=this.level.walls.length;i<l;i++)
          this.draw(this.level.walls[i], "rgb(184, 184, 184)", this.imgObstacule);

          for(var i=0,l=this.level.blocks.length;i<l;i++)
          this.draw(this.level.blocks[i], "rgb(181, 184, 255)");

          for(var i=0,l=this.level.goals.length;i<l;i++)
          this.draw(this.level.goals[i], "rgb(255, 235, 0)", this.imgGoal);

          this.draw(this.level.player, "rgb(130, 212, 130)", this.imgPlayer);
      },
      draw(pos, color, img){
        let $this = this;
          var $row = $($(".f")[pos.y]), $cell = $($(".cell", $row)[pos.x]);              
          if(img !== undefined) {
            $cell.css("background", `url(${img})`);
            $cell.css("background-size", "60px 60px")
          } else {
            $cell.css("background", color);
          }
      },
      bindEvents(){
        let $this = this;
        function nextMoveEmpty(e, dir){
          var px = e.x+dir.x,
              py = e.y+dir.y;

          for(var i=0,l=$this.level.walls.length;i<l;i++){
              if(px == $this.level.walls[i].x && py == $this.level.walls[i].y)
              return false
          }
          for(var i=0,l=$this.level.goals.length;i<l;i++){
              if( px== $this.level.goals[i].x && py == $this.level.goals[i].y)
              return false                    
          }
          return true;
        }

        function nextMoveIsGoal(e, dir){
          var px = e.x+dir.x,
              py = e.y+dir.y;

          for(var i=0,l=$this.level.goals.length;i<l;i++){
              if(px == $this.level.goals[i].x && py == $this.level.goals[i].y)
              return {x:px,y:py,i:i}                   
          }

          return false;
        }

        function nextMoveIsBlock(e, dir){
          var px = e.x+dir.x,
              py = e.y+dir.y;

          for(var i=0,l=$this.level.blocks.length;i<l;i++){
              if(px == $this.level.blocks[i].x && py == $this.level.blocks[i].y)
              return {x:px,y:py,i:i}                   
          }
          return false;
        }

        if($this.playing == true){
          $this.steps.map((step, index) => {
            if(step.done != true){
              if(step.count.length !== 0){
                const steps_to_resolve = step.count.map(to => {
                  //map to array steps
                  if(to.done !== true){
                    if(step.keyCode >= 37 && step.keyCode <= 40){
                      $this.direction = {
                        x : step.keyCode % 2 ? step.keyCode - 38 : 0,
                        y : !(step.keyCode % 2) ? step.keyCode - 39 : 0
                      }

                      if(nextMoveEmpty($this.level.player, $this.direction)){
                        $this.level.player.x += $this.direction.x;
                        $this.level.player.y += $this.direction.y;

                        $this.draw($this.level.player, "rgb(130, 212, 130)");
                        $this.draw({x:$this.level.player.x-$this.direction.x, y:$this.level.player.y-$this.direction.y}, "white");
                      }else{
                        var nint = nextMoveIsGoal($this.level.player, $this.direction);
                        if(nint){
                          if(nextMoveEmpty(nint, $this.direction)){
                            $this.level.goals[nint.i].x = nint.x + $this.direction.x;
                            $this.level.goals[nint.i].y = nint.y + $this.direction.y;
                            $this.level.player.x += $this.direction.x;
                            $this.level.player.y += $this.direction.y;
                            $this.draw({x:$this.level.player.x-$this.direction.x, y:$this.level.player.y-$this.direction.y}, "white");

                            if(nextMoveIsBlock(nint, $this.direction)){
                                $this.checkVictory();
                            }
                          }
                      }
                    }
                    to.done = true;
                    } 
                    $this.update();
                    return false;
                  } else {
                    return true;
                  }
                })
                
              }
            }
               
          })
        } 
      },
      async checkVictory(){
        let $this = this;
        var allInside = true;

        for(var i=0,l=$this.level.goals.length;i<l;i++){
        var inside = false;

        for(var j=0,e=$this.level.blocks.length;j<e;j++){
            if($this.level.blocks[j].x==$this.level.goals[i].x && $this.level.blocks[j].y==$this.level.goals[i].y)
            inside = true;
        }

        allInside = allInside && inside;
        }

        if(allInside){
        var $g = $(".game");

        //animation
        $g.css("width", $g.width());
        $g.css("height", $g.height());
        $(".game .f").remove();

        
        $this.winner = true;
        //save json file to moviments
        let json = {
          user: $this.$store.state.user,
          level: $this.$route.params.game,
          start: $this.start,
          steps: $this.steps,
          end: Date.now(),
          duration: Date.now() - $this.start
        }

        // request to save the progress level 
        let header={"Token" : this.$store.state.token};
        let configuracion= {headers : header};
        let completed = true;
        let _id = $this.$route.params.id
        axios.post('/progreso/update', { _id: _id, completed:completed, status : status } ,configuracion)
        .then(result => {
          axios.post('/juego/upload', { json: json } ,configuracion)
          .then(result => {
            console.log("exito")
            $this.winner = false;
            $this.start = null;
            $this.end = null;
            $this.steps = [];
            $this.$router.push({ path: `/`})
          })
          .catch(e => console.log(e))
        })
        .catch(e => console.log(e))

        
        

        }
      },
      Sokoban(width,heigth){
        let $this = this;

        var init = function(){
            for(var i=0;i<width;i++){
            $(".game").append("<div class='f'></div>");
            var $row = $($(".game .f")[$(".game .f").length-1]);
            for(var j=0;j<heigth;j++){
                $row.append("<div class='cell'></div>");
                if(i==0 || i==width-1 || j==0 || j==heigth-1){
                    $this.level.walls.push({x:i,y:j});
                }    
            }
        }

        $this.update();
        $this.bindEvents();
        return false;
        }
        return init();
      },
      add_step(direction){
        this.start = Date.now()
        if (this.steps.length !== 0 && this.steps[this.steps.length-1].text === direction){
          let sort = this.steps[this.steps.length-1].count;
          let increment = sort[sort.length-1].counter
          this.steps[this.steps.length-1].count.push({
            counter: increment + 1,
            done: false
          })
          
        }else{
          if (direction == 'izquierda'){
            this.steps.push({
              icon: 'arrow_back',
              text: this.dir('izquierda'),
              keyCode: 37,
              count: [{
                counter: 1,
                done: false
              }],
              done: false
            })
          } else if (direction == 'abajo'){
            this.steps.push({
              icon: 'arrow_downward',
              text: this.dir('abajo'),
              keyCode: 40,
              count: [{
                counter: 1,
                done: false
              }],
              done: false
            })
          } else if (direction == 'arriba'){
            this.steps.push({
              icon: 'arrow_upward',
              text: this.dir('arriba'),
              keyCode: 38,
              count: [{
                counter: 1,
                done: false
              }],
              done: false
            })
          } else if (direction == 'derecha'){
            this.steps.push({
              icon: 'arrow_forward',
              text: this.dir('derecha'),
              keyCode: 39,
              count: [{
                counter: 1,
                done: false
              }],
              done: false
            })
          } else {
            console.log("error")
          }
        }
      },
      play(){
        this.playing = true;
        this.bindEvents()
      },
      dir(dir){
        return this.$t(dir)
      }
    },
    computed: {
      idioma(){
        return this.$store.state.idioma
      }
    },
    watch: {
      idioma(val){
        if(val == 'en'){
          this.steps.map(step => {
            if(step.text == 'izquierda'){
              step.text = 'left'
            } else if(step.text == 'derecha'){
              step.text == 'right'
            } else if (step.text == 'arriba'){
              step.text == 'up'
            } else if(step.text == 'abajo'){
              step.text == 'abajo'
            }
          })
        }
      }
    }
}
</script>
<style>
