    color: white;
    text-align: center;
    color: white;
    font-family: Arial, sans-serif;
    background-color: gray;
    display: inline-block;
    margin: 0;
    border: solid 5px rgba(0,0,0,0.4);
    border-radius: 5px;
    padding: 0 2px;
    padding-bottom: 2px;
    box-shadow: 3px 4px 3px rgba(0,0,0,0.31);
    overflow: hidden;
}
.f{
    margin: 5px 0px;
    padding: 0px;
    height: 60px;
    width: 600px;
    color: blue;
    font-size: 1.2em;
}
.cell{
    width:60px;
    height: 60px;
    border-radius: 5px;
    background-color: white;
    display: inline-block;
    margin: 1px;
    padding: 0px;
    color: black;
    transition: all 1s ease-out;
    animation: moviment 5s infinite normal;
}

